import React from 'react'

import Banner from './banner/components/Banner';
import Reputation from './reputation/components/Reputation';
import Services from './services/components/Services';
import Industry from './industry/components/Industry';
import Testimonials from './../../../common/Testimonials/components/Testimonials';
import News from './news/components/News';
import Case from './../../../common/Case/components/Case';

export default function MainPage({caseGallery, testimonialsGallery}) {
    return (
        <div className="main main--pt-0">
            <Banner />
            <Reputation />
            <Services />
            <Industry />
            <Case gallery={caseGallery} particles={true} />
            <Testimonials gallery={testimonialsGallery} />
            <News />      
        </div>
    )
}
