import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap/gsap-core';

export default function SlideIn({children, setXPercent, toXPercent, opacity, ...props}) {

    let item = useRef(null);

    useEffect(() => {

        gsap.set(item.current, {
            xPercent: setXPercent,
            [opacity ? 'autoAlpha' : '']: 0,
        });
    
        gsap.to(item.current, {
            scrollTrigger: {
                trigger: item.current,
                start: 'top center',
            },
            duration: 0.3,
            xPercent: toXPercent || 0,
            [opacity ? 'autoAlpha' : '']: 1,
            ease: 'power1.out'
        });
    }, [setXPercent, toXPercent, opacity]);
  
    return (
        <div {...props} ref={item}>
            {children}
        </div>
    )
}
