import React, { useEffect, useRef } from 'react'
import CustomParticles from './../../../common/CustomParticles/CustomParticles';
import { gsap } from 'gsap/gsap-core';

import Form from '../../../common/Form';
import Button from '../../../common/Button';
import SideText from './../../../common/SideText';
import mailAnimation from '../../../animation/mail.json';
import phoneAnimation from '../../../animation/phone.json';
import geoAnimation from '../../../animation/geo.json';
import Lottie from 'react-lottie';
import LadderText from '../../../common/LadderText';
import animateTextLine from './../../../services/animateTextLine';

const mailOptions = {
    loop: false,
    autoplay: false,
    animationData: mailAnimation,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
    }
};

const phoneOptions = {
    loop: false,
    autoplay: false,
    animationData: phoneAnimation,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
    }
};

const geoOptions = {
    loop: false,
    autoplay: false,
    animationData: geoAnimation,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
    }
};

function playAnimation(wrapper) {
    wrapper.anim.setDirection(1);
    wrapper.play();
}

function stopAnimation(wrapper) {
    wrapper.anim.setDirection(-1);
    wrapper.play();
}

export default function Contact() {
    let lottieMail = useRef(null);
    let lottiePhone = useRef(null);
    let lottieGeo = useRef(null);
    let textRef = useRef([]);
    const ladderText = useRef([]);

    useEffect(() => {
        animateTextLine(textRef.current);
        initLadderAnimation(ladderText.current);
    }, []);

    return (
        <main className="contact main">
            <CustomParticles modificator="contact" />
            <div className="contact__wrapper container" id="contact-wrapper">
                <SideText modificator="contact" wrapper="contact-wrapper">contact</SideText>
                <LadderText refs={ladderText} className="contact__title" text="Contact our Head Office" />
                <ul className="contact__list">
                    <li className="contact__item">
                        <a href="tel:18005647483" className="contact__item-link"
                            onMouseOver={() => {
                                playAnimation(lottiePhone.current);
                            }}
                            onMouseLeave={() => {
                                stopAnimation(lottiePhone.current);
                            }}
                        >
                            <div className="contact__image-wrapper">
                                <Lottie options={phoneOptions} width="75%" height="75%" ref={lottiePhone} />
                            </div>
                            <div className="contact__item-column">
                                <h3 className="contact__item-title">Call Us Today </h3>
                                <div className="contact__item-text">1 (800) 564-7483</div>
                            </div>
                        </a>
                    </li>
                    <li className="contact__item">
                        <a href="mailto:info@thinkbound.com" className="contact__item-link"
                            onMouseOver={() => {
                                playAnimation(lottieMail.current);
                            }}
                            onMouseLeave={() => {
                                stopAnimation(lottieMail.current);
                            }}
                        >
                            <div className="contact__image-wrapper">
                                <Lottie options={mailOptions} width="75%" height="75%" ref={lottieMail} />
                            </div>
                            <div className="contact__item-column">
                                <h3 className="contact__item-title">Email Us NOW</h3>
                                <div className="contact__item-text">info@thinkbound.com</div>
                            </div>
                        </a>
                    </li>
                    <li className="contact__item">
                        <div className="contact__item-link"
                            onMouseOver={() => {
                                playAnimation(lottieGeo.current);
                            }}
                            onMouseLeave={() => {
                                stopAnimation(lottieGeo.current);
                            }}
                        >
                            <div className="contact__image-wrapper">
                                <Lottie options={geoOptions} width="75%" height="75%" ref={lottieGeo} />
                            </div>
                            <div className="contact__item-column">
                                <h3 className="contact__item-title">Head Office</h3>
                                <div className="contact__item-text">40 Wynford Drive, Unit 103</div>
                            </div>
                        </div>
                    </li>
                </ul>
                <div className="line-text contact__line-text">
                    <div className="line-text__row" ref={el => textRef.current.push(el)}>Let’s discuss making your project a reality.</div>
                </div>
                <Form className="form--contact" Button={<Button type="link" text="Send Us Message" className="form__button" />} />
            </div>
            <div className="contact__map-wrapper">
                <iframe className="contact__map" title="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2883.3374220473384!2d-79.33793088427534!3d43.724316855846624!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89d4cd09e766292d%3A0xd753673901d255db!2zNDAgV3luZm9yZCBEciwgTm9ydGggWW9yaywgT04gTTNDIDFKNSwg0JrQsNC90LDQtNCw!5e0!3m2!1sru!2sua!4v1626094699256!5m2!1sru!2sua" allowFullScreen loading="lazy" />
            </div>
        </main >
    )
}

function initLadderAnimation(ladderTextElements) {


    return gsap.timeline({
        defaults: {
            delay: 0.2,
            duration: 0.4,
            ease: 'Power4.in'
        }
    }).from(ladderTextElements, {
        yPercent: 100,
        stagger: 0.1
    })
}
