import React, { useEffect, useRef, useState } from 'react'
import SwiperCore, { Mousewheel, Keyboard } from 'swiper';
import { Swiper } from 'swiper/core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

SwiperCore.use([Keyboard, Mousewheel]);

export default function SectionSwiper({ sections, className }) {

    let wrapper = useRef(null);
    let [scrollState, setScrollState] = useState({ bottom: false, top: false });

    function handleWheel(e) {
        if (scrollState.bottom && e.deltaY > 0) {
            let wrapperPosition = wrapper.current.getBoundingClientRect().bottom;
            window.scrollTo({
                top: window.scrollY + wrapperPosition,
                behavior: 'smooth'
            });
            setScrollState((scrollState) => {
                return { ...scrollState, bottom: false }
            });
            document.body.style.overflow = null;
            setTimeout(() => {
                initScrollTrigger('top');
            }, 500);
        } else if (scrollState.top && e.deltaY < 0) {
            let wrapperPosition = wrapper.current.getBoundingClientRect().top;
            window.scrollTo({
                top: window.scrollY - (wrapperPosition + window.innerHeight),
                behavior: 'smooth'
            });
            setScrollState((scrollState) => {
                return { ...scrollState, top: false }
            });
            document.body.style.overflow = null;
        }
    }

    function initScrollTrigger(direction) {
        ScrollTrigger.create({
            trigger: wrapper.current,
            start: direction === 'bottom' ? 'top center' : 'bottom center',
            [direction === 'bottom' ? 'onEnter' : 'onEnterBack']: () => {
                let wrapperPosition = wrapper.current.getBoundingClientRect().top;
                document.body.style.overflow = 'hidden';
                window.scrollTo({
                    top: window.scrollY + wrapperPosition,
                    behavior: 'smooth'
                })
            },
        });
    }

    useEffect(() => {
        initScrollTrigger('bottom');
        initSwiper(sections, setScrollState);
    }, [sections]);

    return (
        <div className={'sections ' + (className ? 'sections--' + className : '')} ref={wrapper} onWheel={handleWheel}>
            <div className='sections__pagination'>
                <h2 className='sections__pagination-title'>The Breakthrough</h2>
                <ul className='sections__pagination-list' id='sections-swiper-pagination'></ul>
            </div>
            <div className='swiper sections__swiper'>
                <div className='swiper-container sections__swiper-container' id='sections-swiper'>
                    <ul className='swiper-wrapper sections__swiper-wrapper'>
                        {sections.map(item => {
                            return (
                                <li className='swiper-slide sections__swiper-slide' key={item.id}>
                                    <h1 className='sections__swiper-title'>
                                        <img src={item.icon} className='sections__swiper-icon' alt=''></img>
                                        <span>{item.title}</span>
                                    </h1>
                                    <p className='sections__swiper-text'>{item.text}</p>
                                    <img src={item.image} className='sections__swiper-image' alt=''></img>
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </div>
        </div>
    );
}

function initSwiper(sections, setScrollState) {
    let swiper = new Swiper(`#sections-swiper`, {
        slidesPerView: 1,
        direction: 'vertical',
        mousewheel: {
            forceToAxis: true,
            eventsTarget: 'container'
        },
        keyboard: true,
        pagination: {
            el: `#sections-swiper-pagination`,
            clickable: true,
            renderBullet: (index, className) => {
                return (
                    `<li class='sections__pagination-item ${className}'>
                        <button class='sections__pagination-button'>${sections[index].title}</button>
                    </li>`
                );
            }
        },
    });

    swiper.on('reachEnd', () => {
        setTimeout(() => {
            setScrollState((scrollState) => {
                return { ...scrollState, bottom: true }
            })
        }, 500)
    })

    swiper.on('reachBeginning', () => {
        setTimeout(() => {
            setScrollState((scrollState) => {
                return { ...scrollState, top: true }
            })
        }, 500)
    })
}