import React from 'react'

export default function Form({className, Button}) {
    return (
        <form className={"form " + className || ""}>
            <div className="input-wrapper input-wrapper--form input-wrapper--50">
                <label htmlFor="name" className="input-wrapper__label input-wrapper__label--white">Name*</label>
                <input className="input input-wrapper__input" id="name" />
            </div>
            <div className="input-wrapper input-wrapper--form input-wrapper--50">
                <label htmlFor="company" className="input-wrapper__label input-wrapper__label--white">Company*</label>
                <input className="input input-wrapper__input" id="company" />
            </div>
            <div className="input-wrapper input-wrapper--form input-wrapper--50">
                <label htmlFor="email" className="input-wrapper__label input-wrapper__label--white">Email*</label>
                <input className="input input-wrapper__input" id="email" />
            </div>
            <div className="input-wrapper input-wrapper--form input-wrapper--50">
                <label htmlFor="phone" className="input-wrapper__label input-wrapper__label--white">Phone*</label>
                <input className="input input-wrapper__input" id="phone" />
            </div>
            <div className="input-wrapper input-wrapper--form">
                <label htmlFor="service" className="input-wrapper__label input-wrapper__label--white">Interested Service*</label>
                <div className="select-wrapper">
                    <select className="select-wrapper__select input" id="service">
                        <option value="0"></option>
                        <option value="1">Option 1</option>
                        <option value="2">Option 2</option>
                    </select>
                </div>
            </div>
            <div className="input-wrapper input-wrapper--form">
                <label htmlFor="comment" className="input-wrapper__label input-wrapper__label--white">Comment</label>
                <textarea className="input input--textarea input-wrapper__input input-wrapper__input--textarea" id="comment"></textarea>
            </div>
            {Button}  
        </form>
    )
}
