import React from 'react'
import Date from './../../../../../common/Date';

export default function TestimonialsCard({ item }) {
    return (
        <a href={item.href} className="card card--news">
            <div className="card__background" style={{ backgroundImage: `url(${item.background})` }}></div>
            <div className="card__wrapper">
                <h1 className="card__title">{item.title}</h1>
                <Date date={item.date} />
            </div>
        </a>
    )
}
