import React from 'react'
import CustomParticles from '../../../common/CustomParticles/CustomParticles';
import ParallaxBanner from '../../../common/ParallaxBanner'
import SideText from '../../../common/SideText';

import bg from '../../../images/case-study/bg.png';
import bg2 from '../../../images/case-study/bg2.png';
import bg3 from '../../../images/case-study/bg3.png';
import image from '../../../images/case-study/image.png';
import icon from '../../../images/case-study/icon.png';
import SectionSwiper from '../../../common/SectionSwiper/SectionSwiper';
import itemImg1 from '../../../images/case-study/img1.png';
import itemImg2 from '../../../images/case-study/img2.png';
import itemImg3 from '../../../images/case-study/img3.png';
import cmc from '../../../images/case-study/cmc.png';
import Arrow from './../../../common/icons/Arrow';
import { Link } from 'react-router-dom';

let sections = [
    {
        title: 'Drupal Migration ',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 1
    },
    {
        title: 'Product Assembly',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 2
    },
    {
        title: 'Integration',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 3
    },
    {
        title: 'Submission Portal',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 4
    },
    {
        title: 'Website Optimization',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 5
    }
];

export default function CaseStudy() {
    return (
        <main className="main case-study">
            <ParallaxBanner title="CMC: Candaian Music Centre" animateTitle={true} bg={bg} className="banner--case-study" scrollToID="case-study-wrapper" percent={window.innerWidth < 768 ? 500 : 300} />
            <div className="case-study__wrapper" id="case-study-wrapper">
                <CustomParticles modificator="case-studies" />
                <div className="case-study__columns container" id="columns">
                    <SideText modificator="case-study" wrapper="columns" percent={10}>case studies</SideText>
                    <div className="case-study__column case-study__column--1">
                        <h1 className="case-study__title case-study__title--columns case-study__title--v1">The Challange</h1>
                        <p className="case-study__text">
                            The Canadian Music Centre holds Canada's largest collection of Canadian concert music. With an ever-growing resource library containing tens of thousands of records, CMC was seeking an expert team to implement an innovative and secure approach to managing and sharing the works of Canadian composers online via their new WordPress website. CMC approached Thinkbound to make their enduring vision a reality.
                        </p>
                    </div>
                    <div className="case-study__column case-study__column--2">
                        <h3 className="case-study__title case-study__title--columns case-study__title--small">The Work</h3>
                        <ul className="case-study__work-list">
                            <li className="case-study__work-item">Drupal Migration</li>
                            <li className="case-study__work-item">Product Assembly</li>
                            <li className="case-study__work-item">Integration</li>
                            <li className="case-study__work-item">Submission Portal</li>
                            <li className="case-study__work-item">Website Optimization</li>
                        </ul>
                    </div>
                </div>
                <SectionSwiper sections={sections} className="case-study" />
                <ParallaxBanner title="The Result" bg={bg2} className="banner--result" percent={20} id="result-banner" />
                <ul className="case-study__list container">
                    <li className="case-study__item">
                        <img src={itemImg1} className="case-study__item-image" alt="" />
                        <div className="case-study__item-column">
                            <h2 className="case-study__item-title">Software</h2>
                            <p className="case-study__item-text">
                                The CMC was left with a leaner operational model with all its software and components working together, along with major collaboration improvements organization-wide. Through the use of Thinkbound’s systems, internal and external customers rely significantly less on IT and are refocussed on their organizational goals.
                            </p>
                        </div>
                    </li>
                    <li className="case-study__item">
                        <img src={itemImg2} className="case-study__item-image" alt="" />
                        <div className="case-study__item-column">
                            <h2 className="case-study__item-title">Roadmapping</h2>
                            <p className="case-study__item-text">
                                Detailed feedback sessions led to creative problem solving through flowcharts and architecture diagrams and developing roadmaps and implementation of ideal scenarios.
                            </p>
                        </div>
                    </li>
                    <li className="case-study__item">
                        <img src={itemImg3} className="case-study__item-image" alt="" />
                        <div className="case-study__item-column">
                            <h2 className="case-study__item-title">Thinking Beyond</h2>
                            <p className="case-study__item-text">
                                Thinkbound maintains an innovation role within the organization as the digital lead for all enhancements and modifications to the CMC’s various platforms. With daily communication and weekly reporting.
                            </p>
                        </div>
                    </li>
                </ul>
                <div className="case-study__feedback">
                    <div className="case-study__feedback-wrapper container">
                        <h1 className="case-study__title case-study__title--feedback">Client Feedback</h1>
                        <p className="case-study__feedback-text">What makes Thinkbound stand out is their ability to create solutions that don’t otherwise exist.</p>
                        <p className="case-study__feedback-user">Glenn Hodgins - President and CEO</p>
                        <img src={cmc} className="case-study__feedback-image" alt="" />
                    </div>
                </div>
                <Link to="/" className="banner banner--next">
                    <div className="banner__image" style={{backgroundImage: `url(${bg3})`}}></div>
                    <div className="banner__wrapper container">
                        <h1 className="banner__title banner__title--big">Next Project</h1>
                        <h3 className="banner__title banner__title--small">Box Me Up </h3>
                        <div className="banner__title banner__title--arrow">
                            <span className="banner__title-text">View Case study</span>
                            <Arrow className="banner__title-icon" />
                        </div>
                    </div>
                </Link>
            </div>
        </main>
    )
}
